:root {
  --background-color: #fafafa;
  --box-shadow: 13px 13px 26px #c8c8c8, -13px -13px 26px #fff;
  --text-color: #121212;
  --desc-color: #525252;
  --border: 1px solid hsla(0, 0%, 100%, 0.12);
  --surface-color: #f3f3f3;
  --stepper-color: #1565ff;
  --skeleton-color: #ebebeb;
  --percent: 50;
}
@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap");
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: var(--background-color); */
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
span {
  margin: 0;
}

a {
  color: var(--text-color);
  text-decoration: none;
}

.background {
  padding: 10px;
  max-width: 1300px;
  margin: 0 auto;
  position: relative;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background-color: #303030;
    --box-shadow: 13px 13px 26px #262626, -13px -13px 26px #3a3a3a;
    --text-color: #ffffff;
    --desc-color: rgba(255, 255, 255, 0.65);
    --border: 1px solid rgba(255, 255, 255, 0.12);
    --secondary-color: #979797;
    --surface-color: rgba(255, 255, 255, 0.12);
    --stepper-color: #1565ff;
    --skeleton-color: #5a5a5a;
  }
}
